@charset "utf-8";

// Import a Google Font
@import url('https://fonts.googleapis.com/css?family=Nunito:400,700');

// Set your brand colors
$purple: #8a4d76;
$pink: #fa7c91;
$brown: #757763;
$beige-light: #d0d1cd;
$beige-lighter: #eff0eb;
$red: #E12626;

// Update Bulma's global variables
$family-sans-serif: 'Nunito', sans-serif;
$grey-dark: $brown;
$grey-light: $beige-light;


// Import only what you need from Bulma
@import '../node_modules/bulma/sass/utilities/_all.sass';
@import '../node_modules/bulma/sass/base/_all.sass';
@import '../node_modules/bulma/sass/helpers/_all.sass';
@import '../node_modules/bulma/sass/elements/button.sass';
@import '../node_modules/bulma/sass/elements/container.sass';
@import '../node_modules/bulma/sass/form/_all.sass';
@import '../node_modules/bulma/sass/elements/box.sass';
@import '../node_modules/bulma/sass/elements/title.sass';
@import '../node_modules/bulma/sass/elements/button.sass';
@import '../node_modules/bulma/sass/components/level.sass';
@import '../node_modules/bulma/sass/components/navbar.sass';
@import '../node_modules/bulma/sass/layout/section.sass';
@import '../node_modules/bulma/sass/elements/icon.sass';

@media (max-width: $tablet) {
  .section {
    padding: 3rem 0.5rem; } }

.text-overflow-rtl {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  direction: rtl;
  min-width: 10px;
  max-width: 75%; }

.text-overflow-ltr {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  direction: ltr;
  min-width: 10px;
  max-width: 80%; }

.has-mid-algm {
  display: flex;
  align-items: center; }

.put-right {
  margin-left: auto; }

.put-left {
  margin-right: 0;
  margin-left: 0; }


.container-login {
  @extend .container;
  max-width: 600px; }

.modal-buttons {
  @extend .buttons;
  justify-content: flex-end; }


.is-fab {
  width: 100%;
  position: fixed;
  bottom: 5%; }

.has-content-centered {
  display: flex;
  justify-content: center; }

.entrada-dato {
  width: 300px;
  padding-top: 5px;
  padding-bottom: 5px; }

.cuerpo-simple {
  padding-top: 15px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around; }

.footer-buttons {
  justify-content: space-around;
  width: 100%; }


.cargando-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width : 100%;
  min-height: 80vh;
  height: 100%; }


.absolute-center-80vh {
  display: flex;
  justify-content: center;
  align-items: center;
  width : 100%;
  height: 100%;
  min-height: 80vh; }

.absolute-center {
  display: flex;
  justify-content: center;
  align-items: center;
  width : 100%;
  height: 100%; }

.mid-just {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.has-shadow {
  box-shadow: 10px 10px 10px -5px #CCCCCC; }

.box-row {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.list-box {
  @extend .box;
  padding: 0.75em !important;
  margin-bottom: 0.5em !important; }

.resumen {
  background: $primary;
  margin-top: 0.75em;
  padding-left: 1em;
  padding-right: 1em;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  margin-bottom: 5px !important;
  .heading {
    @extend .is-size-7;
    color: $beige-lighter; }
  .title {
    @extend .is-size-6;
    color: $white; } }

.btn-guardado {
  margin: 1.5em; }

//especificos

.duracion {
  padding-top: 8px;
  padding-bottom: 8px; }



